cc.Class({
    extends: cc.Component,

    properties: {
       btnTurboOn: cc.Button,
       btnTurboOff: cc.Button,
    },

    onLoad(){
        this.turboOff();
    },

    turboOn() {
        this.btnTurboOn.interactable = false;
        this.btnTurboOff.interactable = true;
    },

    turboOff() {
        this.btnTurboOn.interactable = true;
        this.btnTurboOff.interactable = false;
    },
});
